/**
 * Breakpoints
 */
$breakpoints: (
        xs: 500px,
        sm: 767px,
        md: 991px,
        lg: 1200px,
        xl: 1400px,
);

/**
 * Body
 */
$font-body                      :   'Josefin Sans', sans-serif;
$font-title                     :   'Oswald', sans-serif;
$color-body                     :   #1C3360;
$color-h                        :   #1C3360;
$textSize-body                  :   16px;
$textLineHeight-body            :   27px;
$backgroundColor-body           :   #fff;
$fontWeight-body                :   400;

$textSize-body-medium           :   14px;
$textLineHeight-body-medium     :   22px;

/**
 * Container
 */
$container-padding              :   30px;
$container-maxWidth             :   1330px;
/**
 * Link
 */
$color-link                     :   #1C3360;

/**
