/**
 * Global
 */
.visible {
  visibility: visible;
  opacity: 1; }

.invisible {
  visibility: hidden;
  opacity: 0; }

.withoutMP {
  margin: 0;
  padding: 0; }

.animated {
  transition: all .25s ease; }
  .animated-long {
    transition-duration: 0.36s; }
  .animated-delay {
    transition-delay: 0.36s; }

.flex {
  display: flex;
  flex-wrap: wrap; }

.flex-horizontalCenter {
  display: flex;
  flex-wrap: wrap;
  align-items: center; }

.flex-verticalCenter {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center; }

.flex-center {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center; }

.flex-spaceBetween {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.flex-end {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end; }

.flex.row:before, .flex.row:after, .flex.container:before, .flex.container:after,
.flex-horizontalCenter.row:before,
.flex-horizontalCenter.row:after,
.flex-horizontalCenter.container:before,
.flex-horizontalCenter.container:after,
.flex-verticalCenter.row:before,
.flex-verticalCenter.row:after,
.flex-verticalCenter.container:before,
.flex-verticalCenter.container:after,
.flex-center.row:before,
.flex-center.row:after,
.flex-center.container:before,
.flex-center.container:after,
.flex-spaceBetween.row:before,
.flex-spaceBetween.row:after,
.flex-spaceBetween.container:before,
.flex-spaceBetween.container:after {
  display: none; }

.alignCenter {
  text-align: center; }

.alignRight {
  text-align: right; }

.disabled {
  pointer-events: none; }

img.alignleft {
  float: left;
  margin-right: 70px; }

img.alignright {
  float: right;
  margin-left: 70px; }

.clear {
  *zoom: 1; }
  .clear:after {
    content: " ";
    display: table;
    clear: both; }
  .clear:before {
    content: " ";
    display: table; }

.visible-mobile {
  display: none; }

@media (max-width: 767px) {
  .visible-mobile {
    display: block; }
  .hidden-mobile {
    display: none; } }

