/**
 * Classes Buttons
 *
 * btn
 *
 */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0px 30px;
  font-weight: 600;
  font-size: 16px;
  line-height: 14px;
  border-radius: 999px;
  text-decoration: none;
  border: 2px solid transparent;
  text-transform: uppercase;
  height: 50px;
  letter-spacing: -0.02em;
  transition: all 0.12s linear;
  text-align: center; }
  .btn-opacity {
    background-color: transparent;
    border-color: #DA1B5A; }
    .btn-opacity:hover {
      color: #fff;
      border-color: #DA1B5A;
      background-color: #DA1B5A; }
  .btn-gradient {
    color: #fff;
    border: none;
    background: linear-gradient(110.3deg, #DA1B5A 34.05%, #E5AF2A 86.98%);
    position: relative;
    z-index: 1; }
    .btn-gradient::after {
      content: '';
      position: absolute;
      left: 0px;
      top: 0px;
      right: 0px;
      bottom: 0px;
      background: linear-gradient(100.3deg, #FCC62B 34.05%, #DA1B5A 86.98%);
      opacity: 0;
      transition: all 2s ease-in-out;
      border-radius: 999px;
      z-index: -1; }
    .btn-gradient:hover {
      color: #fff; }
      .btn-gradient:hover::after {
        opacity: 1; }
  .btn-color-white {
    color: #fff; }
  .btn-lg {
    height: 57px;
    line-height: 18px; }
  .btn span {
    position: relative;
    top: 2px; }

